import { createApp } from 'vue';
import App from './App.vue';
import '@fortawesome/fontawesome-free/css/all.css';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faCoffee, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import router from './router';

library.add(faCoffee, faEnvelope); // Ajoutez les icônes à la bibliothèque

const app = createApp(App);
app.use(router); // Utilise Vue Router dans ton application

app.component('font-awesome-icon', FontAwesomeIcon); // Définissez le composant pour utiliser les icônes
app.mount('#app');
