<template>
  <div class="quick-view-container" :style="backgroundImageStyle">
    <div class="content">
      <h2>{{project.name}}</h2>

      <div class="icons">
        <div class="icon">
          <i class="fas fa-heart front"></i>
          <p>{{project.tags.front.length}}</p>
        </div>

        <div class="icon">
          <i class="fas fa-star back"></i>
          <p>{{project.tags.back.length}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QuickView",
  props: {
    project: {
      required: true
    }
  },
  data() {
    return {
      backgroundSize: 'contain',
    };
  },
  computed: {
    backgroundImageStyle() {
      // Vérifiez si project.image est défini
      if (this.project.image) {
        // Utilisez l'URL de l'image comme fond
        return {
          backgroundImage: `url('${this.project.image}')`,
        };
      } else {
        // Aucune image n'est définie, utilisez un fond par défaut ou laissez-le vide
        return {};
      }
    },
  },
}
</script>

<style scoped>
.quick-view-container {
  aspect-ratio: 1 / 1;

  display: flex;
  flex-direction: row;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--light-grey);
}

.content{
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;

  gap: 8px;
  padding: 20px 0;

  color: var(--white);
}

.content:hover{
  background-color: var(--shadow-medium);
}

.content h2{
  text-align: center;
}

.quick-view-container .content{
  opacity: 0;
}

.quick-view-container:hover .content{
  opacity: 1;
}

.icons{
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;
}

.icon{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6px;

  flex: 1;
  font-size: 24px;

  color: var(--white);
}

.quick-view-container p{
  font-size: 18px;
}

.front{
  color: var(--front-color)
}

.back{
  color: var(--back-color)
}

</style>
