    <div class="header">
      <tabs :tabs="tab" @tab-selected="changeTab" :active="this.selectedTab"></tabs>
    </div>

    <div class="projects">
      <div v-if="selectedTab === 0" class="quick-view">
        <QuickView v-for="project in projects" :key="project" :project="project">
        </QuickView>
      </div>

      <div v-else-if="selectedTab === 1" class="detailed-view">
        <DetailedView v-for="project in projects" :key="project" :project="project"></DetailedView>
      </div>
    </div>
  </div>
</template>

<script>
import Tabs from "@/components/Tabs";
import DetailedView from "@/components/DetailedView";
import QuickView from "@/components/QuickView";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Preview",
  components: {QuickView, DetailedView, Tabs },
  data() {
    return {
      projects: [
        {name: 'Portfolio',image: require('@/assets/Portfolio.png'), tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'Calorie',image: require('@/assets/calorie.png'), tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'Timer',image: require('@/assets/timer.png'), tags: {front: ['React Native',], back: []}},
        {name: 'E-commerce',image: require('@/assets/e-commerce.png'), tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
        {name: 'It\'s A Watch !',image: require('@/assets/Watch.png'), tags: {front: ['Flutter',], back: []}},
        {name: 'Chifoumi',image: require('@/assets/chifoumi.png'), tags: {front: ['XML',], back: ['Java']}},
        {name: 'GoToTown',image: require('@/assets/GoToTown.png'), tags: {front: ['XML',], back: ['Java']}},
        {name: 'Lykhubs',image: require('@/assets/Lykhubs.png'), tags: {front: ['HTML', 'CSS', 'Javascript',], back: []}},
        {name: 'Lumberjack',image: require('@/assets/Haptique.png'), tags: {front: ['Qt'], back: ['C++']}},
        {name: 'Twitch Extension',image: require('@/assets/Extension.png'), tags: {front: ['VueJS', 'HTML', 'CSS', 'Javascript',], back: ['Javascript']}},
      ],
      tab: ['fa-th', 'fa-square'],
      selectedTab: 0
    }
  },
  methods: {
    changeTab(tab) {
      this.selectedTab = tab;
    }
