<style scoped>
.preview-container {
  display: flex;
  flex-direction: column;

  flex: 1;

  margin: 20px 20px 0 20px;

  overflow-y: auto;
}

.header {
  height: 35px;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  background-color: var(--medium-grey);
}

.projects {
  display: flex;

  flex: 1;

  overflow-y: auto;
  overflow-x: hidden;
}

.detailed-view {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 30px;

  flex: 1;

  padding: 10px 25px;

  overflow-y: auto;

  background-color: var(--medium-grey);

}

.quick-view{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1px;

  flex: 1;

  overflow-y: auto;
}

/* CUSTOM SCROLLBAR */
/* Custom scrollbar track */
.detailed-view::-webkit-scrollbar-track, .quick-view::-webkit-scrollbar-track {
  background-color: var(--light-grey);
}

/* Custom scrollbar thumb */
.detailed-view::-webkit-scrollbar-thumb, .quick-view::-webkit-scrollbar-thumb {
  background-color: var(--dark-grey);
}

/* Hide the default scrollbar */
.detailed-view::-webkit-scrollbar, .quick-view::-webkit-scrollbar {
